@font-face {
  font-family: Montserrat;
  src: url('../../public/montserrat/Montserrat-Medium.otf');
}

@font-face {
  font-family: Gotham;
  src: url('../../public/Gotham-Font/Gotham-Black.otf');
}

body {
  background-color: #e8def4;
  margin: 0;
  padding: 0;
  overflow-x: hidden;
  overflow-y: auto;
  font-family: 'Montserrat';
}

.main-body {
  margin-left: 5%;
  border-left: 2px;
}

button {
  cursor: pointer;
  padding: 4px 20px;
  margin: 4px 2px;
}

.banner-button {
  background-color: transparent;
  background-repeat: no-repeat;
  border: none;
  overflow: hidden;
  outline: none;
  background: none;
}

.nav-item {
  text-decoration: none;
  font-family: Gotham;
}

.nav-item:hover {
  text-decoration: underline;
}

.icon-img {
  background: transparent;
}

.icon-button {
  cursor: pointer;
  opacity: 50%;
  transition-property: opacity;
  transition-duration: 1s;
  background-color: transparent;
  font-size: 2em;
  height: auto;
  object-fit: cover;
}

.icon-button:hover {
  opacity: 100%;
}

