.header {
  overflow: hidden;
  border-radius: 0;
  border-bottom-right-radius: 0;
  background-color: #f7cd64;
  padding: 5px 8px;
  border-right: 2px solid whitesmoke;
  position: fixed;
  height: 100%;
  flex-direction: column;
  width: 5%;
  top: 0;
}

a.nav-item {
  color: #5e4579;
  font-size: 1.2em;
  text-underline-offset: 0.2em;
  overflow: auto;
}

.banner-content {
  display: flex;
  align-items: center;
  flex-direction: column;
}

.banner-content:hover {
  flex-direction: column;
}

.header-bottom {
  justify-content: flex-end;
  align-items: center;
  flex-direction: column;
}

.flex-grow {
  flex-grow: 0.75;
}

.flex {
  display: flex;
}

.text-body {
  margin-left: 10vw;
  margin-top: 20vh;
  flex-direction: column;
}

.text {
  padding: 5px 8px;
  max-width: 30vw;
  letter-spacing: 0.1em;
}